.body-bg {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../images/events-section/mob-bg.jpg);

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/events-section/mob-bg@2x.jpg);
  }

  @media screen and (min-width: 768px) {
    & {
      background-image: url(../images/events-section/tabl-bg.jpg);

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/events-section/tabl-bg@2x.jpg);
      }
    }
  }

  @media screen and (min-width: 1280px) {
    & {
      background-image: url(../images/events-section/desk-bg.jpg);

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/events-section/desk-bg@2x.jpg);
      }
    }
  }
}
